import {UsersAPI} from "../API/api";
import {updateObjectArray} from "../utils/object-helpers";

const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const SET_USERS = 'SET_USERS';
const SET_CURRENT_PAGE = 'SET_CURRENT_PAGE';
const SET_TOTAL_USERS_COUNT = 'SET_TOTAL_USERS_COUNT';
const TOGGLE_IS_FETCHING = 'TOGGLE_IS_FETCHING';
const TOGGLE_IS_FOLLOWING_PROGRESS = 'TOGGLE_IS_FOLLOWING_PROGRESS';

let initialState = {
    users: [],
    pageSize: 10,
    totalUsersCount: 0,
    currentPage: 1,
    isFetching: false,
    followingInProgres: [],
}

const usersReducer = (state = initialState, action) => {
    switch (action.type) {
        case FOLLOW:
            return {
                ...state,
                users: updateObjectArray(state.users, action.userID, 'id', {followed: false})

            }

        case UNFOLLOW:
            return {
                ...state,
                users: updateObjectArray(state.users, action.userID, 'id', {followed: true})

            }
        case  SET_USERS:
            return {
                ...state, users: action.users
            }
        case  SET_CURRENT_PAGE:
            return {
                ...state, currentPage: action.currentPage
            }
        case  SET_TOTAL_USERS_COUNT:
            return {
                ...state, totalUsersCount: action.count
            }
        case  TOGGLE_IS_FETCHING:
            return {
                ...state, isFetching: action.isFetching
            }
        case  TOGGLE_IS_FOLLOWING_PROGRESS:
            return {
                ...state,
                followingInProgres: action.isFetching
                    ? [...state.followingInProgres, action.userId]
                    : state.followingInProgres.filter(id => id !== action.userId)
            }
        default:
            return state;
    }


}

export const acceptFollow = (userID) => {
    return {
        type: FOLLOW,
        userID
    }
}
export const acceptUnfollow = (userID) => {
    return {
        type: UNFOLLOW,
        userID
    }
}
export const setUsers = (users) => {
    return {
        type: SET_USERS,
        users
    }
}
export const setCurrentPage = (currentPage) => {
    return {
        type: SET_CURRENT_PAGE,
        currentPage
    }
}
export const setTotalUsersCount = (totalUsersCount) => {
    return {
        type: SET_TOTAL_USERS_COUNT,
        count: totalUsersCount
    }
}
export const toggleIsFetching = (isFetching) => {
    return {
        type: TOGGLE_IS_FETCHING,
        isFetching
    }
}
export const toggleFollowingProgres = (isFetching, userId) => {
    return {
        type: TOGGLE_IS_FOLLOWING_PROGRESS,
        isFetching,
        userId
    }
}

export const requestUsers = (currentPage, pageSize) => async (dispatch) => {
    dispatch(toggleIsFetching(true))

    let data = await UsersAPI.getUsers(currentPage, pageSize)
    dispatch(toggleIsFetching(false))
    dispatch(setUsers(data.items))
    dispatch(setTotalUsersCount(data.totalCount))

}

const followUnfollowFlow = async (dispatch,userId,apiMethod,ActionCreator) => {

    dispatch(toggleFollowingProgres(true, userId));
    let response = await apiMethod(userId)

    if (response.data.resultCode === 0) {
        ActionCreator(userId)
    }
    dispatch(toggleFollowingProgres(false, userId));
}

export const follow = (userId) => async (dispatch) => {

    followUnfollowFlow(dispatch,userId,UsersAPI.followUser.bind(UsersAPI),acceptFollow)

}

export const unfollow = (userId) => async (dispatch) => {

    followUnfollowFlow(dispatch,userId,UsersAPI.unFollowUser.bind(UsersAPI),acceptFollow)
}

export default usersReducer;