const SEND_MESSAGE = 'SEND-MESSAGE';

let initialState = {
    dialogs: [
        {id: 1, name: 'Alex', avaLink: '/assets/img/avatars/alex.jpg'},
        {id: 2, name: 'Nika', avaLink: '/assets/img/avatars/nika.jpg'},
        {id: 3, name: 'Dima', avaLink: '/assets/img/avatars/dima.jpg'},
        {id: 4, name: 'Vika', avaLink: '/assets/img/avatars/vika.jpg'},
        {id: 5, name: 'Sergey', avaLink: '/assets/img/avatars/sergei.jpg'},
        {id: 6, name: 'Ineza', avaLink: '/assets/img/avatars/ineza.jpg'}
    ],
    messages: [
        {id: 1, message: 'Hi!'},
        {id: 2, message: 'Hi! How are you?'},
        {id: 3, message: 'Yo!'},
        {id: 4, message: 'Cool!'},
        {id: 5, message: 'You?'},
        {id: 6, message: 'Great!'},
        {id: 7, message: 'Have a good day!'},
        {id: 8, message: 'And you too!'},
        {id: 9, message: 'What you will do tomorow?'},
        {id: 10, message: 'I will go to the school. And you?'},
        {id: 11, message: `I'd like to go to the theatre`},
        {id: 12, message: 'Sounds great!'},
    ]
}

const dialogsReducer = (state = initialState, action) => {
    switch (action.type) {
        case SEND_MESSAGE: {
            let body = action.newMessageBody;
            return {
                ...state,
                messages : [...state.messages, {id: 13, message: body}],

            }
        }
        default:
            return state;
    }
}

export const sendMessage = (newMessageBody ) => {
    return {
        type: SEND_MESSAGE,
        newMessageBody
    }
}

export default dialogsReducer;