import React from "react";
import classes from "./Navbar.module.css"
import {NavLink} from "react-router-dom";
import Friends from "./Friends/Friends";


const Navbar = (props) => {

    let friendsElements = props.friends
        .map(friend => <Friends key={friend.id} id={friend.id} name={friend.name} avaLink={friend.avaLink}/>)

    return (
        <nav className={classes.nav}>
            <div>
                <NavLink to="/profile" className={classes.item} activeClassName={classes.active}>
                        <img alt="Profile" src={process.env.PUBLIC_URL + "/assets/img/icons/profile.png"}/>
                        <p>Profile</p>
                </NavLink>
            </div>
            <div>
                <NavLink to="/dialogs" className={classes.item} activeClassName={classes.active}>
                    <img alt="Messages" src={process.env.PUBLIC_URL + "/assets/img/icons/message.png"}/>
                    <p>Messages</p>
                </NavLink>
            </div>
            <div>
                <NavLink to="/users" className={classes.item} activeClassName={classes.active}>
                    <img alt="Users" src={process.env.PUBLIC_URL + "/assets/img/icons/user.png"}/>
                    <p>Users</p>
                </NavLink>
            </div>
            <div>
                <NavLink to="/news" className={classes.item} activeClassName={classes.active}>
                    <img alt="News" src={process.env.PUBLIC_URL + "/assets/img/icons/news.png"}/>
                    <p>News</p>
                </NavLink>
            </div>
            <div>
                <NavLink to="/music" className={classes.item} activeClassName={classes.active}>
                    <img alt="Music" src={process.env.PUBLIC_URL + "/assets/img/icons/music.png"}/>
                    <p>Music</p>
                </NavLink>
            </div>
            <div>
                <NavLink to="/settings" className={classes.item} activeClassName={classes.active}>
                    <img alt="Settings" src={process.env.PUBLIC_URL + "/assets/img/icons/settings.png"}/>
                    <p>Settings</p>
                </NavLink>
            </div>
            <div className={classes.friends}>
                <h3>Friends</h3>
                {friendsElements}
            </div>
        </nav>
    )
}

export default Navbar