import React from "react";
import classes from "./Header.module.css"
import {NavLink} from "react-router-dom";

const Header = (props) => {
    return (
        <header className={classes.header}>
            <NavLink to="/">
                <img
                    alt="logo"
                    src={process.env.PUBLIC_URL + "/assets/img/logo.png"}/>
                <p>My Social Network</p>
            </NavLink>
            {props.isAuth ?
                <>
                    {/*<NavLink className={classes.loginBlock} to={'/profile'}><p>{props.login}</p></NavLink>*/}
                    <span className={classes.loginBlock} onClick={props.logout}><p>Logout</p></span>
                </>
                : <NavLink className={classes.loginBlock} to={'/login'}><p>Login</p></NavLink>}

        </header>
    )
}

export default Header